@import 'variables';


.main {
    &__slider {
        position: relative;
        max-width: 100%;
        min-width: 100%;
        max-height: calc( 100vh - 80px );
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 90px;
        &__text {
            position: absolute;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            align-items: center;
            max-height: 500px;
            min-height: 400px;
            &__h2 {
                font-family: 'mainBold';
                font-size: 40px;
                color: #fff;
                position: relative;
                display: flex;
                flex-direction: column;
                align-items: center;
                &__underline {
                    @include underline; } }
            &__h1 {
                font-family: 'decorative';
                font-size: 70px;
                color: #ffffff;
                position: relative;
 }                // padding-top: 180px
            &__button {
                @include button;
                &:hover {
                    @include button__hover; } } } }
    &__sales {
        max-height: 100vh;
        min-width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        &__h1 {
            font-family: 'mainBold';
            font-size: 40px;
            display: flex;
            flex-direction: column;
            align-items: center;
            &__underline {
                @include underline;
                margin-bottom: 55px; } }
        &__content {
            display: flex;
            width: 100%;
            flex-direction: row;
            justify-content: space-around;
            margin-bottom: 80px;
            &__sale1, &__sale2 {
                display: flex;
                flex-direction: column;
                align-items: center;
                &__img {
                    margin-bottom: 20px; }
                &__h3 {
                    font-family: 'mainBold';
                    font-size: 30px;
                    margin-bottom: 10px; }
                &__p {
                    font-family: 'mainLight';
                    font-size: 16px;
                    color: #000000a3;
                    text-align: center;
                    max-width: 476px;
                    margin-bottom: 15px; }
                &__button {
                    @include button;
                    &:hover {
                        @include button__hover; } } } }
        &__credit {
            width: calc( 100vw - 500px );
            height: 180px;
            background-color: $main-color;
            border-radius: 75px;
            margin-bottom: 10px;
            display: flex;
            align-items: center;
            justify-content: center;
            box-shadow: inset 8px 8px 7px rgba(0, 0, 0, 0.25);
            &__content {
                display: flex;
                align-items: center;
                justify-content: space-around;
                width: 100%;
                &__text {
                    // display: flex
                    // flex-direction:
                    &__h3 {
                        font-family: 'mainLight';
                        font-size: 32px;
                        text-align: center;
                        text-transform: uppercase;
                        color: #000000a3;
                        margin-bottom: 15px; }
                    &__p {
                        font-family: 'main';
                        font-size: 24px;
                        text-align: center;
                        color: #000000a3; } } } } } }





